




























import Vue from 'vue'
import { Component, Prop } from 'vue-property-decorator'
import { BookDto } from '@/services/models.ts'
import AuthorSelect from '@/pages/component/AuthorSelect.vue'

@Component({
  components: {
    AuthorSelect
  }
})
export default class BookGeneralCard extends Vue {
  @Prop({ type: Object, required: true }) readonly value!: BookDto

  id = 0

  onChangeName(value: string) {
    this.value.title = value
  }
  onChangeText(value: string) {
    this.value.text = value
  }
}
