import { VuexModule, Module, Mutation, getModule } from 'vuex-module-decorators'
import store from '@/store'
import { AuthorDto } from '@/services/models'
import { Action } from 'vuex-module-decorators'
import api from '@/services/api'

export interface AuthorState {
  authors: Array<AuthorDto>
  loading: boolean
}

@Module({ store, dynamic: true, name: 'author' })
class AuthorModule extends VuexModule implements AuthorState {
  authors: Array<AuthorDto> = new Array<AuthorDto>()
  loading = false

  public get getAuthors() {
    return this.authors
  }

  @Mutation
  public updateAuthors(payload: Array<AuthorDto>) {
    this.authors = payload
  }

  @Mutation
  private updateLoading(payload: boolean) {
    this.loading = payload
  }

  @Action({ rawError: true })
  public async loadAuthors() {
    if (this.loading) return
    console.log('its working')

    const authors = await api.authors.getAll()
    this.updateAuthors(authors)
    this.updateLoading(true)
  }

  @Action({ rawError: true })
  public async updateList() {
    const authors = await api.authors.getAll()
    this.updateAuthors(authors)
  }
}

export const authorModule = getModule(AuthorModule)
