









import { AuthorDto } from '@/services/models'
import { authorModule } from '@/store/modules/authorModule'
import Vue from 'vue'
import { Component, Prop } from 'vue-property-decorator'

@Component
export default class AuthorSelect extends Vue {
  @Prop({ required: true, type: Number }) value!: number
  authors: Array<AuthorDto> = new Array<AuthorDto>()

  async created() {
    await authorModule.loadAuthors()
    this.authors = authorModule.getAuthors
  }
}
