








import Vue from 'vue'
import { Component, Prop, Watch } from 'vue-property-decorator'

import BookGeneralCard from '@/pages/bookPage/BookGeneralCard.vue'
import { BookDto } from '@/services/models'
import api from '@/services/api'

@Component({
  components: {
    BookGeneralCard
  }
})
export default class Book extends Vue {
  @Prop({ type: Number, required: true }) id!: number
  book: BookDto = new BookDto()

  async created() {
    this.book = await api.books.getById(this.id)
  }

  @Watch('book', {
    deep: true
  })
  async onModelChange(value: BookDto, oldValue: BookDto) {
    if (oldValue && oldValue.id) {
      await api.books.update(this.id, value)
    }
  }
}
